@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
.app .accessory-details-sidebar {
  overflow-y: scroll;
}

.app .accessories-section {
  height: 100%;
  box-sizing: border-box;
}
.app .accessories-section-inner {
  display: flex;
  background-color: white;
  height: 100%;
  box-sizing: border-box;
}
.app .model-image-container {
  flex-basis: calc(100% - var(--sidebar-width));
  display: flex;
  align-items: center;
  padding-right: 3%;
}

.app .sidebar-menu-1,
.app .sidebar-menu-2 {
  height: 100%;
  width: 100%;
  position: absolute;
  will-change: transform;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 0;
}
.app .sidebar-menu-1 {
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.app .sidebar-menu-1.active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.app .sidebar-menu-2 {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}
.app .sidebar-menu-2.active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.app .accessories-sidebar {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  padding: 0px 0px 0px 20px;
}

.sidebar-inner {
  width: 100%;
  height: 100%;
  position: relative;
  overflow-x: hidden;
}

.app .accessories-accordian-container {
  margin-left: 2%;
}

.app .weak-label {
  color: var(--grey-3);
}
.app .accordian-category-name {
  color: var(--grey-4);
  font-size: 16px;
  display: block;
  font-weight: bold;
  letter-spacing: -0.5px;
}

.app .accessories-accordian {
  overflow-y: scroll;
  max-height: 600px;
}

.app .primary-button.accessory-add-new {
  width: 100%;
  justify-content: center;
  margin-bottom: 16px;
}

.app .accessories-sidebar-header {
  margin-bottom: 10px;
}

.app .accordion-item-header {
  display: flex;
  justify-content: flex-start;
  cursor: pointer;
  align-items: center;
}
.app .accordion-item-header h3,
.app .accordion-item-header path {
  color: var(--grey-6);
  fill: var(--grey-6);
  font-size: 18px;
  line-height: 18px;
  margin: 8px 0;
  font-weight: 400;
  font-family: var(--heading-font);
}
.app .accordion-item-header:hover h3,
.app .accordion-item-header:hover path {
  color: var(--accent-dark);
  fill: var(--accent-dark);
}
.app .accordion-item .svg-icon-container {
  font-size: 22px;
  line-height: 0;
}
.app .accordion-item .svg-icon-container svg {
  transition: all 200ms;
}
.app .accordion-item.open .svg-icon-container svg {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.app .accordion-content-wrapper {
  overflow: hidden;
  transition: max-height 200ms;
  /* box-sizing: content-box; */
  padding: 0 6px;
  margin: 0 -6px;
  will-change: transform;
}
.app .accordion-item-content {
  padding-top: 5px;
  padding-bottom: 5px;
}

.accessory-details {
  display: flex;
}

.left-col,
.right-col {
  width: 50%;
}
.right-col {
  padding: 0 20px;
}

.current-screen-container {
  position: relative;
}

body {
  background-color: #f6f7f9;
  background-color: var(--grey-1);
}
.app .current-screen-container {
  height: calc(
    2 * 10px +
      2 * 312px);
  height: calc(
    var(--product-select-rows) * var(--product-select-gutter) +
      var(--product-select-rows) * var(--product-select-height)
  );
}
.app .current-screen-container .item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  will-change: transform;
}
.app .current-screen-container .item.active {
  position: static;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  right: 0;
  bottom: 0;
  will-change: transform;
}

:root {
  --text: #373a47;
  --black: #373a47;
  --black-1: #282a33;
  --accent-dark: #e41212;
  --accent-dark-trans: #e4121255;
  --accent-dark-hover: #ff1d1d;
  --input-outline-hover: #ff1d1d;
  --white: #ffffff;
  --grey-1: #f6f7f9;
  --grey-1x: #e6e7e9;
  --grey-2: #dde0e6;
  --grey-3: #a5a9be;
  --grey-4: #9599ae;
  --grey-5: #7a7f97;
  --grey-6: #5a5f77;
  --grey-7: #4a4f67;
  --inverse-text: #dde0e6;
  --disabled: #cacaca;
  --box-shadow: #a5a9beaa;
  --confirm: green;
  --warning: red;
  --confirm-bg: #dbf7db;
  --heading-font: "Nunito", sans-serif;
  --body-font: "Nunito", sans-serif;
  --product-select-gutter: 10px;
  --product-min-width: 200px;
  --product-select-rows: 2;
  --sidebar-width: 33%;
  --header-height: 60px;
  --footer-height: 120px;
  --product-select-height: 312px;
  --product-select-height-max: 430px;
  --container-max-width: 1200px;
}

@media (min-width: 1200px) {
  :root {
    --accessory-button-height: 58px;
    --product-select-gutter: 16px;
  }
}
/* custom scrollbar */
::-webkit-scrollbar {
  width: 23px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #9599ae;
  background-color: var(--grey-4);
  border-radius: 20px;
  border: 10px solid transparent;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}

.app img {
  max-width: 100%;
}
/* .app {
  height: 100vh;
} */
.primary-container {
  padding-top: 36px;
}

body .app {
  font-family: "Nunito", sans-serif;
  font-family: var(--heading-font);
  background-color: #f6f7f9;
  background-color: var(--grey-1);
  color: #373a47;
  color: var(--black);
}

.app header {
  width: 100%;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1200px) {
  .app header {
    flex-direction: row;
  }
}

.app p {
  margin: 16px 0;
  font: normal 15px/20px "Nunito", sans-serif;
  font: normal 15px/20px var(--heading-font);
}
.app h1 {
  align-self: center;
  font-size: 18px;
  font-weight: 400;
}
.app .small-caps {
  text-transform: uppercase;
  font-size: 20px;
  color: #7a7f97;
  color: var(--grey-5);
  font-weight: 600;
}
/**** reusable stuff. move this ***/
.app .text-button {
  background-color: transparent;
  display: inline;
  border: 0;
  padding: 0;
}
.app .text-button:hover {
  color: #e41212;
  color: var(--accent-dark);
  cursor: pointer;
}
.app .primary-button:hover,
.app .secondary-button:hover {
  background-color: #ff1d1d;
  background-color: var(--accent-dark-hover);
  cursor: pointer;
}
.app .secondary-button:hover {
  border-color: #ff1d1d;
  border-color: var(--accent-dark-hover);
}

.app .primary-button.disabled,
.app .secondary-button.disabled {
  border-color: #cacaca;
  border-color: var(--disabled);
  background-color: #cacaca;
  background-color: var(--disabled);
  cursor: auto;
  pointer-events: none;
}

.app .primary-button {
  border: 1px solid #e41212;
  border: 1px solid var(--accent-dark);
  background-color: #e41212;
  background-color: var(--accent-dark);
  color: #f6f7f9;
  color: var(--grey-1);
  display: flex;
  padding: 10px 20px;
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  font-family: "Nunito", sans-serif;
}

.app .secondary-button {
  border: 1px solid #a5a9be;
  border: 1px solid var(--grey-3);
  background-color: #a5a9be;
  background-color: var(--grey-3);
  color: #f6f7f9;
  color: var(--grey-1);
  padding: 10px 20px;
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  font-family: "Nunito", sans-serif;
}

.app .outline-button {
  background-color: transparent;
  color: #e41212;
  color: var(--accent-dark);
  padding: 10px 20px;
  border: 1px solid #e41212;
  border: 1px solid var(--accent-dark);
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  cursor: pointer;
  font-family: "Nunito", sans-serif;
}

.app .primary-button svg,
.app .outline-button svg,
.app .secondary-button svg {
  margin-right: 5px;
  font-size: 20px;
}
.app .outline-button.secondary {
  color: #7a7f97;
  color: var(--grey-5);
  border: 1px solid #7a7f97;
  border: 1px solid var(--grey-5);
}
.app .outline-button:hover {
  color: white;
  background-color: #e41212;
  background-color: var(--accent-dark);
  border: 1px solid transparent;
}
.app .primary-button.small,
.app .outline-button.small,
.app .secondary-button.small {
  font-size: 14px;
  padding: 6px 12px;
}
.app .primary-link {
  font-size: 15px;
  font-weight: 600;
  color: #e41212;
  color: var(--accent-dark);
  text-decoration: none;
}
.app .primary-link:hover {
  color: #ff1d1d;
  color: var(--accent-dark-hover);
}
.app .secondary-link {
  font-size: 15px;
  font-weight: 600;
  color: #7a7f97;
  color: var(--grey-5);
  text-decoration: none;
}
.app .secondary-link:hover {
  color: #e41212;
  color: var(--accent-dark);
}
.app .app-container {
  display: block;
  max-width: 1200px;
  max-width: var(--container-max-width);

  margin: 0 auto;
  /* height: calc(100% - var(--header-height)); */
  /* height: 100%; */
}

.app .primary-container main {
  padding: 10px;
}

.app .add-accessories a {
  font-weight: 900;
  text-decoration: none;
  color: #e41212;
  color: var(--accent-dark);
  font-size: 22px;
  text-align: center;
}
.app .add-accessories svg,
.app .add-accessories span {
  vertical-align: middle;
}
.app .add-accessories:hover a {
  color: #ff1d1d;
  color: var(--accent-dark-hover);
}
.app .icon-line {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.app .add-icon {
  margin-right: 0.6rem;
  max-width: 24px;
  width: 24px;
  fill: #e41212;
  fill: var(--accent-dark);
  transition: all 0.3s;
}
.app .add-accessories:hover .add-icon {
  fill: #ff1d1d;
  fill: var(--accent-dark-hover);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.app .add-accessories {
  margin: 1.5rem 0;
}
.app .sidebar {
  flex-basis: 33%;
  flex-basis: var(--sidebar-width);
  background-color: #eef0f5;
}
.app .screen-inner-container {
  background-color: #ffffff;
  height: 100%;
}

.app .step-title-container {
  display: flex;
  height: 40px;
  /* overflow: hidden; */
}

.app .szForm {
  padding-top: 32px;
  margin-bottom: 32px;
  box-sizing: border-box;
}
.app .form-title {
  margin-top: 32px;
  margin-bottom: 16px;
}

.app .szForm label {
  margin-bottom: 16px;
  background-color: white;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.02);
  padding: 0 0 0 12px;
  display: flex;
  align-items: center;
}
.app .szForm .no-bg {
  background-color: transparent;
  box-shadow: none;
  padding: 0;
}
.app .szForm label:focus-within {
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.02),
    0 0 3px var(--input-outline-hover);
}
.app .szForm label.no-bg:focus-within {
  box-shadow: none;
}

.app .szForm input {
  border: 0;
  padding: 14px 10px;
  flex-grow: 1;
}

.app .szForm [type="checkbox"] {
  width: 20px;
  flex-grow: 0;
}

.app .szForm .label-text {
  width: 100px;
  display: inline-block;
}

.app .szForm input:active {
  outline: none;
}

::-webkit-input-placeholder {
  color: var(--grey-3);
}

:-ms-input-placeholder {
  color: var(--grey-3);
}

::-ms-input-placeholder {
  color: var(--grey-3);
}

::placeholder {
  color: var(--grey-3);
}

.app textarea:focus,
.app input:focus {
  outline: none;
}

.app .szForm fieldset {
  border: 0;
  padding: 0;
}

.app .szForm .radio {
  display: block;
}

.app .szForm select {
  padding: 14px 10px;
  border: 0;
  width: 100%;
}

.app .szForm legend {
  font-weight: 700;
  margin-bottom: 0.8rem;
}

.app .model-details-section {
  display: flex;
  background-color: white;
  padding: 2%;
  justify-content: space-around;
}
.app .model-details-section .model-details {
  flex-basis: var(--sidebar-width);
  overflow-y: scroll;
  max-height: 600px;
  margin-left: 2%;
}
.app .model-details-section .model-price {
  color: var(--accent-dark);
  font-size: 24px;
}

.app .preview-image {
  padding: 32px;
  display: block;
  text-align: center;
  height: 170px;
}

.app .preview-image img {
  max-height: 100%;
}

.app .product {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
  max-height: calc(2 * var(--product-select-height));
  box-sizing: border-box;
}

.app .product-thumb-wrapper {
  overflow: hidden;
  /* max-height: var(--product-select-height); */
}

.app img.product-thumb {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

.app .product-listing {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30%, 1fr));
  grid-column-gap: var(--product-select-gutter);
  -webkit-column-gap: var(--product-select-gutter);
          column-gap: var(--product-select-gutter);
  grid-row-gap: var(--product-select-gutter);
  row-gap: var(--product-select-gutter);
  height: 100%;
  grid-template-rows: repeat(auto-fit, minmax(0, 1fr));
}

.app .product.selected .product-short_description {
  opacity: 1;
}
.app .product-short_description {
  margin: 5px 0 0;
}
.app .product-listing .product {
  padding: 25px;
  border: 2px solid var(--white);
  transition: box-shadow 0.5s;
  box-sizing: border-box;
}
.app .product-listing .selected.product {
  background-color: var(--white);
  border: 2px solid var(--accent-dark);
  box-shadow: 0px 0px 8px var(--box-shadow);
}
.app .product-listing .selected.product:hover {
  border: 2px solid var(--accent-dark);
}
.app .product-listing .product:hover {
  cursor: pointer;
  border: 2px solid var(--black);
}
.app h3.product-title {
  font-size: 21px;
  margin-bottom: 0;
}
.app h4.product-price {
  margin: 0;
  font-weight: 600;
}
.app .product-bottom-line {
  margin-top: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.accessories-screen-inner-container {
  background-color: white;
  height: 100%;
  overflow: scroll;
  padding: 22px 30px;
  box-sizing: border-box;
}

.accessories-screen-inner-container .accessory-category-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  grid-row-gap: 10px;
  row-gap: 10px;
}

.accessories-screen-inner-container .accessory-category-content .minicard {
  flex-grow: 0;
  flex-shrink: 0;
}
body .app .accessory-category-title {
  color: var(--grey-4);
  font-weight: normal;
  font-size: 16px;
}
body .app .accessories-screen-inner-container .screen-title {
  margin: 0;
}

#sz-root .app .thankyou-wrapper .h1 {
  font-size: 36px;
  font-weight: bold;
  margin-top: 0;
  color: var(--grey-6);
}

.app .thankyou-wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  padding-bottom: 50px;
  align-items: center;
  background-color: white;
  height: 100%;
  box-sizing: border-box;
  justify-content: center;
}

.app h3.thankyou-productname {
  font-size: 28px;
}
.app .thankyou-prod-name {
  color: var(--accent-dark);
}
.app .thankyou-accessories {
  font-size: 20px;
}
.app .thankyou-product-image img {
  height: 100%;
}
.app .thankyou-product-image {
  height: 250px;
}
.app .thankyou-box-price {
  font-size: 28px;
  font-weight: bold;
  color: var(--accent-dark);
}
.app .thankyou-buttons {
  margin-top: 16px;
}

.app .thankyou-buttons button {
  margin: 0 0.25rem 32px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

