.accessories-screen-inner-container {
  background-color: white;
  height: 100%;
  overflow: scroll;
  padding: 22px 30px;
  box-sizing: border-box;
}

.accessories-screen-inner-container .accessory-category-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 20px;
  row-gap: 10px;
}

.accessories-screen-inner-container .accessory-category-content .minicard {
  flex-grow: 0;
  flex-shrink: 0;
}
body .app .accessory-category-title {
  color: var(--grey-4);
  font-weight: normal;
  font-size: 16px;
}
body .app .accessories-screen-inner-container .screen-title {
  margin: 0;
}
